<template>
  <div class="comments-container">
    <h1>Comments</h1>
    <input type="text" v-model="newComment" placeholder="Enter a comment">
    <button @click="addComment">Add Comment</button>
    <ul class="comment-list">
      <li v-for="comment in comments" :key="comment.id" class="comment-item">
        {{ comment }}
      </li>
    </ul>
  </div>
</template>

<script>
import Stomp from 'stompjs-websocket';
import SockJS from 'sockjs-client';

export default {
  data() {
    return {
      newComment: '',
      comments: []
    };
  },
  mounted() {
    // Connect to WebSocket STOMP message broker
    const socket = new SockJS('http://localhost:8080/comments');
    const stompClient = Stomp.over(socket);

    stompClient.connect({}, () => {
      // Subscribe to the "comments" destination
      stompClient.subscribe('/topic/comments', (message) => {
        const comment = JSON.parse(message.body);
        
        console.log("PAOK: "+ JSON.stringify(comment));

        this.comments = comment; // Assign received comment(s) to the comments array
      });
    });
  },
  methods: {
    addComment() {
      // Create a new comment object
      const comment = {
        text: this.newComment
      };

      // Send the comment to the "comments" destination
      const socket = new SockJS('http://localhost:8080/comments');
      const stompClient = Stomp.over(socket);

      stompClient.connect({}, () => {
        stompClient.send('/addComment', {}, JSON.stringify(comment));
        stompClient.disconnect();
      });

      // Clear the input field
      this.newComment = '';
    }
  }
};
</script>

<style scoped>
.comments-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
}

.comment-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.comment-item {
  width: 100%;
  margin-bottom: 10px;
